import '../index.css'
import './timer'

export default function Countdown () {
    return (
        <div className='text-red-700 bg-white w-full h-1/2 '>
            <h1 className='text-center'>Countdown</h1>
            
            <div className='text-center md:mx-72 sm:mx-0 py-16'>
                <ul className='grid grid-cols-4 gap-1 md:gap-6 justify-center items-center'>
                    <li>
                        <div className='bg-white rounded-lg shadow-lg h-32 md:h-48 flex flex-col items-center justify-center'>
                            <h1 id ='day'></h1>
                            <p>Days</p>
                        </div>
                        
                    </li>
                    <li>
                        <div className='bg-white rounded-lg shadow-lg h-32 md:h-48 flex flex-col items-center justify-center'> 
                            <h1 id ='hour'></h1>
                            <p>Hours</p> 
                        </div>
                    </li>
                    <li>
                        <div className='bg-white rounded-lg shadow-lg h-32 md:h-48 flex flex-col items-center justify-center'>
                            <h1 id ='minute'></h1>
                            <p>Minutes</p>
                        </div>
                        
                    </li>
                    <li>
                        <div className='bg-white rounded-lg shadow-lg h-32 md:h-48 flex flex-col items-center justify-center'>
                            <h1 id='second'></h1>
                            <p>Seconds</p>
                        </div>
                        
                    </li>
                </ul>
            </div>
            

        </div>
    )
}